.buyout {
    &__list-cont {
        background-color: #f6d700;
    }

    &__list {
        display: flex;
justify-content: center;    
gap: 140px;    
padding-top: 30px;
padding-bottom: 30px;

@media (max-width: 960px) {
    flex-direction: column;
    gap: 30px;
}

 @media (max-width: 640px) {
      max-width: 600px;
      width: 100%;
    }
    }

    &__item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        gap: 20px;
    }


    &__title {
        font-size: 24px;
  line-height: 30px;
  margin-bottom: 25px;
  margin-top: 18px;
  font-weight: 500;
  color: #000000;

   @media (max-width: 640px) {
     font-weight: 400;
    }
    }
}