.supply-main-header {
  background-color: #ebebeb;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 90px;
  row-gap: 100px;
  overflow: hidden;

  @media (max-width: 1024px) {
    gap: 5px;
  }

  & .container {
    display: flex;
    align-items: center;
    padding-bottom: 60px;
    gap: 110px;
    max-width: 100%;
    margin: 0 auto;

    @media (max-width: 1024px) {
      justify-content: space-between;
      gap: 10px;
    }

    @media (max-width: 960px) {
      flex-direction: column;
      margin-top: 100px;
      gap: 30px;
    }

    @media (max-width: 768px) {
      padding: 0 25px;
      align-items: baseline;
      margin-bottom: 60px;
    }

    @media (max-width: 480px) {
      padding: 0 20px;
    }
  }

  &__text-cont {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 460px;

    @media (max-width: 960px) {
      width: 600px;
    }

    @media (max-width: 480px) {
      width: auto;
    }
  }

  &__title {
    font-size: 42px;
    line-height: 1.23;
    font-weight: 400;

    @media (max-width: 1200px) {
      font-size: 38px;
    }

    @media (max-width: 960px) {
      width: 600px;
      font-size: 38px;
    }

    br {
      display: none;
    }

    @media (max-width: 768px) {
      font-size: 32px;
      width: 500px;
    }

    @media (max-width: 480px) {
      width: auto;
      font-size: 30px;
    }
  }

  & .line2 {
    width: 50px;
    height: 3px;
    background-color: #222222;
  }

  &__text {
    max-width: 500px;
    font-size: 20px;
    line-height: 1.55;
    font-weight: 300;

    @media (max-width: 1200px) {
      font-size: 18px;
    }

    @media (max-width: 960px) {
    }

    @media (max-width: 768px) {
      font-size: 16px;
      line-height: 1.45;
    }
  }

  .supply-main-header__img {
    height: 560px;
    background-position: center;
    background-size: cover;
    object-fit: cover;

    @media (max-width: 1200px) {
      width: 460px;
    }

    @media (max-width: 960px) {
      width: 600px;
    }

    @media (max-width: 768px) {
      width: 544px;
    }
  }

  & .line1-cont {
    max-width: 1160px;
    height: 1.5px;
    float: none;
    display: inline-block;
    margin-left: 20px;
    margin-right: 20px;
    width: 60%;
    position: relative;
    background-color: #f6d700;
    display: block;

    @media (max-width: 1200px) {
      height: 1px;
    }

    @media (max-width: 480px) {
      height: 0.5px;
      width: 1%;
    }

    &::before {
      content: '';
      position: absolute;
      background-image: linear-gradient(to right, #f6d700, rgba(246, 215, 0, 0));
      display: block;
      min-width: 200px;
      height: 1.5px;
      right: -120px;
      z-index: 20;
      @media (max-width: 1200px) {
        height: 1px;
      }

      @media (max-width: 480px) {
        height: 0.5px;
        right: -170px;
      }
    }

    &::after {
      content: '';
      position: absolute;
      background-image: linear-gradient(to left, #f6d700, rgba(246, 215, 0, 0));
      display: block;
      min-width: 200px;
      height: 1.5px;
      left: -120px;
      z-index: 20;
      @media (max-width: 1200px) {
        height: 1px;
      }

      @media (max-width: 480px) {
        height: 0.5px;
        left: -170px;
      }
    }
  }
}
